<template>
  <div class="home">
    <div class="leftside">
      <div class="shapes">
        <img src="@/assets/images/方糖形状参照表.png" alt="">
      </div>

      <div class="counts">
        <div class="input" v-for="(item,index) in shapes" :key="index">
          <label>{{item.name}}:</label>
          <el-input-number size="mini" v-model="item.count" :min="0" :max="9" label="描述文字"></el-input-number>
        </div>
      </div>
    </div>



    <div class="rightside">
      <span>当前已选择方糖匹配的方案数量：{{possibleNum}}</span>
      <br>
      <err-button @click="show">
        show
      </err-button>
      <err-button @click="showPossibleArr = false">
        close
      </err-button>
      <err-button @click="reset">
        重置
      </err-button>

      <ul v-show="showPossibleArr">
        <li v-for="(item,index) in interimArr" :key="'s'+index">
          {{item.toString()}}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import {debounce} from 'lodash'
export default {
  name: 'HomeView',
  data() {
    return {
      shapes: [],
      possibleArr: [],
      showPossibleArr: false,
      interimArr: []
    }
  },
  beforeMount() {
    // 初始化
    this.init();
  },
  methods: {
    init() {
      for (let i = 1; i <= 19; i++) {
        this.shapes.push({ name: `形状${i}`, count: 0, desc: i + '' })
      }
    },
    reset() {
      this.shapes= [];
      this.possibleArr= [];
      this.showPossibleArr= false;
      this.interimArr= [];
      this.init();
    },
    show() {
      if (this.possibleArr.length > 10000) return;
      this.showPossibleArr = true;
      this.interimArr = this.possibleArr;
    },
    filterUseData: debounce(function () {
      let interimArr = [];
      this.shapes.forEach(shape => {
        if (shape.count > 0) {
          interimArr = interimArr.concat(window.effectiveData.filter(item => this.arrIncludeElementCount(item, shape.desc) >= shape.count))
        }
      })
      this.possibleArr = interimArr;
    }, 600),
    // 当前已选择的形状数量
    selectedShapesNum() {
      let i = 0;
      this.shapes.forEach(item => {
        i = i + item.count;
      });
      return i;
    },
    arrIncludeElementCount(arr, element) {
      let count = 0;
      arr.forEach(item => {
        if (item == element) count++;
      });
      return count;
    }
  },
  watch: {
    shapes: {
      deep: true,
      immediate: false,
      handler() {
        if (this.selectedShapesNum() > 0) {
          this.filterUseData();
        } else {
          this.possibleArr = [];
        }
      }
    }
  },
  computed: {
    possibleNum() {
      return this.possibleArr.length;
    }
  }
}
</script>

